.container{
    width: 70%;
    height: calc(100vh - 80px);
    margin: 0 auto;
    background: center / contain no-repeat url('./img/static_assets_rpm6.png');

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 100px;

}
.container h1, h3{
    text-align: center;
}
.container h1{
    font-size: 4em;
    text-transform: uppercase;
}
.container h3{
    font-size:2em;
}

.container__date{
    display: flex;
    justify-content: space-evenly;
    margin-top: 100px;
}

.container__date .date{
    width: 200px;
    height: 150px;
    box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.4);
    background-color: #fff;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container__date .date h2{
    font-size: 4em;
}