.navbar__container{
    width: 100%;
    height: 80px;
    background-color: var(--dark-color);
    display: flex;
    justify-content: space-between;
    padding: 0 95px;

}

.link__container{
    display: flex;
    align-items: center;

}
.link__container li{
    list-style: none;
    padding: 30px 48px;
    color: #fff;
    position: relative;
    text-align: center;
    cursor: pointer;
}

.link__container .nav__link.active,
.link__container .nav__link:hover::after{
    content: '';
    display: block;
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: var(--orange-color);
    margin-top: 27px;
}